import React from "react";
import Layout from "../components/layout";
import Seo from "../components/seo";
import Breadcrumb from "../components/common/breadcrumb";
import EnterpriseBlockchainScreen from "../screens/Enterprise-blockchain";
import { initializeApp } from "firebase/app";
import { getFirestore } from "firebase/firestore/lite";
import { graphql } from "gatsby";

const EnterpriseBlockchainPage = (props) => {
  const app = initializeApp(props?.data?.site?.siteMetadata?.firestore);
  const db = getFirestore(app);
  const breadcrumbLinks = [
    { text: "Home", link: "/" },
    { text: "Enterprise Blockchain Development Company", link: null },
  ];
  return (
    <Layout db={db}>
      <Breadcrumb items={breadcrumbLinks} />
      <EnterpriseBlockchainScreen db={db} />
    </Layout>
  );
};

export const Head = () => (
  <Seo
    title="Enterprise Blockchain Development Services"
    headTitle="Enterprise Blockchain Development Company | Enterprise Blockchain"
    description="Are you seeking enterprise blockchain solutions? JustTry Tech.com  provides superior-quality services in the development of enterprise blockchain. Reach out to us now!"
    keywords="Enterprise Blockchain Development, Custom Blockchain Solutions, Blockchain Consulting Services, Permissioned Blockchain Development, Consortium Blockchain Solutions, Private Blockchain Development, Hyperledger Fabric Development, Corda Blockchain Development, Ethereum Enterprise Solutions, Smart Contract Development for Enterprises, Tokenization Services, Decentralized Identity Solutions, Supply Chain Blockchain Development, Healthcare Blockchain Solutions, Financial Services Blockchain, Blockchain Integration for Enterprises, Cross-Industry Blockchain Solutions, Enterprise Blockchain Architecture, Blockchain Security Services, Identity Management on Blockchain, Blockchain Governance, Enterprise Blockchain Interoperability, Industry-Specific Blockchain Solutions"
  ></Seo>
);

export default EnterpriseBlockchainPage;

export const queryData = graphql`
  query MyQuery {
    site {
      siteMetadata {
        firestore {
          apiKey
          appId
          authDomain
          messagingSenderId
          projectId
          storageBucket
        }
      }
    }
  }
`;
